import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IArticulo } from '../models/articulo';
import { ArticulosService } from '../service/articulos.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { MatButton } from '@angular/material/button';
import { SaveComponent } from './save/save.component';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { ModalComponent } from './modal/modal.component';
import { IFilter } from '@shared/models/filter';
import { environment } from 'environments/environment';
import { MatDialog } from '@angular/material/dialog';
import { SearchService } from 'app/layout/common/search/search.service';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { StockComponent } from './stock/stock.component';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { PwaService } from '@shared/service/app/pwa.service';
@Component({
    selector: 'app-articulos',
    templateUrl: './articulos.component.html',
    styleUrls: ['./articulos.component.scss']
})
export class ArticulosComponent implements OnInit, OnDestroy {
    articulos: IArticulo[];
    selectedArticulos: any[] = [];
    public dataSource: MatTableDataSource<IArticulo> = new MatTableDataSource();
    column_params = ['seleccion','codigo_interno', 'descripcion', 'descripcion_rubro', 'descripcion_tipo', 'activo', 'total_stock', 'cantidad_pendiente', 'acciones']
    headers = ['','Código Interno', 'Descripción', 'Rubro', 'Tipo Articulo', 'Activo', 'Stock', 'Pendiente de entrega','Acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['descripcion', 'codigo_interno'];
    filter: IFilter = {
        activo: -1,
        tipo: -1,
        rubro: -1
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.stock.view_articulos + '?sort=descripcion&pagination=1&sort=-total_stock';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        private _articulosService: ArticulosService,
        private _empresaService: EmpresaService,
        private _menuService: JedsMenuService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private router: Router,
        public _headerTextService: HeaderTextService,
        private pwaService: PwaService
    ) {
        this._headerTextService.setTitulo(this.router);

        /**
        * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
        */
        this.funcionesObjeto = [
            {
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (articulo: IArticulo ) => 'jedstion:editar',
                iconoAccionado: (articulo: IArticulo) => '',
                iconoDeshabilitado: (articulo: IArticulo) => '',
            },
            {
                nombre_boton: "Unidad de medida",
                functionName: 'um',
                iconoAccion: (articulo: IArticulo ) => 'jedstion:unidad_medida',
                iconoAccionado: (articulo: IArticulo) => '',
                iconoDeshabilitado: (articulo: IArticulo) => '',
            },
            {
                nombre_boton: 'Stock',
                functionName: 'stock',
                iconoAccion: (articulo: IArticulo ) => 'jedstion:stock',
                iconoAccionado: (articulo: IArticulo) => '',
                iconoDeshabilitado: (articulo: IArticulo) => '',
            },
            {
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (articulo: IArticulo ) => 'jedstion:eliminar',
                iconoAccionado: (articulo: IArticulo) => '',
                iconoDeshabilitado: (articulo: IArticulo) => '',
            }
        ];
    }
    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;


    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(250),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    /**
    * Esta funcion se encarga de levantar los datos que se van a filtrar en el componente general del filtro
    * @param origin 
    */
    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.activo != -1 ? "filter[activo]=" + this.filter.activo : '';
                        this.extraParams = this.filter.tipo != -1 ? "filter[tipo]=" + this.filter.tipo : '';
                        this.extraParams = this.filter.rubro != -1 ? "filter[idrubro]=" + this.filter.rubro : '';
                        this.tabla.filters(this.filtroBusqueda);
                        // this._searchService.getSearchInput().setValue(this._tabsServices.getFiltroBuscadorPedido());
                    }
                },
            });
    }

    /**
    * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
    * @param event 
    */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateArticulo(event);
                break;
            case 'um':
                this.modalUnidadMedidas(event);
                break;
            case 'stock':
                this.stockArticulo(event);
                break;
            case 'eliminar':
                this.deleteArticulo(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveArticulo() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                    this.alert.success('Artículo creado con éxito');
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updateArticulo(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                    this.alert.success('Artículo editado con éxito');
                },
            });
    }

    /**
    * Funcion en el componente
    * - Esta funcion abre un modal.
    * @param documentRef
    * @param articulo 
    */
    modalUnidadMedidas(event) {
        this.dialogo
            .open(ModalComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.tabla.renderTabla();
                    this.alert.success('Unidad de Medida creada con éxito');
                },
            });
    }

    /**
     * Esta funcion muestra el stock que se tiene de ese articulo en cada uno de los Depositos
     */
    stockArticulo(event) {
        this.dialogo.open(StockComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false})
        .afterClosed()
        .subscribe({
            next: () => {
                this.tabla.renderTabla();
            }
        })
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteArticulo(event) {
        this.confirm.open({
            title: "Eliminar Artículo " + event.params.data.descripcion,
            message: "¿Desea eliminar la Artículo: " + event.params.data.descripcion + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._articulosService.deleteArticulo(event.params.data.idarticulo).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Artículo eliminado con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error(error);
                            this.alert.warning("Este Artículo no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    /**
     * Esta función es la que se encarga de la seleccion de check box de artículos seleccionados
     * @param selectedElements - La lista de artículos seleccionados.
     */
    onSelectionChange(selectedElements: any[]) {
        this.selectedArticulos = selectedElements;
    }

    /**
     * Esta funcion Genera un Reporte de Etiqueta/s y lo muestra en una nueva pestaña
     */
    reporteEtiqueta() {
        if (!this.selectedArticulos || this.selectedArticulos.length === 0) {
            this.alert.error('Debe seleccionar al menos un artículo.');
            return;
        } else {
            let payload = null; 
            payload = {
                articulos: this.selectedArticulos.map(element => ({ idarticulo: element.idarticulo }))
            };
        
            this._articulosService.reporteEtiqueta(payload).subscribe(
                (data) => {
                    if (data.success) {
                        const reportePath = data.path;
                        if (this.pwaService.isPwa()) {
                            this.router.navigate(['/reporte', reportePath]);
                        } else {
                            const reporteUrl = `${environment.baseRest}/${reportePath}`;
                            window.open(reporteUrl, '_blank'); 
                        }
                    } else {
                        this.alert.error('Los artículos deben tener al menos una Unidad de Medida asignada.');
                    }
                },
                (error) => {
                    console.error('Error en la solicitud del reporte de etiquetas', error);
                    this.alert.error('Los artículos deben tener al menos una Unidad de Medida asignada.');
                }
            );
        }
    }    
    
    ngOnDestroy(): void {
        this._searchService.unsubscribe()
    }
}