import { Component, Inject, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ArticulosService } from '../../service/articulos.service';
import { IArticulo } from '../../models/articulo';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { Subject, firstValueFrom, timer } from 'rxjs';
import { IAlertConfig } from '@shared/models/alert';
import { takeUntil, tap, finalize } from 'rxjs/operators';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-modal-stock',
    templateUrl: './stock.component.html',
    styleUrls: ['./stock.component.scss']
})
export class StockComponent implements OnInit {
    columns = ['deposito', 'cantidad'];
    title: string;
    articulo: IArticulo;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private articulosService: ArticulosService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<StockComponent>,
        private confirm: FuseConfirmationService,
    ) {
        this.articulo = this.data ? this.data : null;
        this.title = `Stock de: ${this.articulo.descripcion}`;
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.getArticulosStock();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1800)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    async getArticulosStock() {
        const articulo_stock = await firstValueFrom(this.articulosService.getArticulosStockTable(this.articulo.idarticulo));
        this.getDataStockTable(articulo_stock);
    }

    getDataStockTable(data) {
        // Agrupar y ajustar los datos
        const adjusteData = this.groupAndAdjustData(data.list);
        
        // Calcular el total combinado
        let totalCantidad = adjusteData.reduce((total, item) => {
            const cantidadParts = item.cantidad.split(' + ');
            cantidadParts.forEach(part => {
                const match = part.match(/(\d+)\s*(.*)/);
                if (match) {
                    const cantidad = parseInt(match[1]);
                    total[match[2].trim()] = (total[match[2].trim()] || 0) + cantidad;
                }
            });
            return total;
        }, {});
    
        // Formatear el total para mostrarlo en la tabla
        const totalFormatted = Object.entries(totalCantidad)
            .map(([um, cantidad]) => `${cantidad} ${um}`)
            .join(' + ');
    
        // Agregar el total como último elemento si hay datos
        if (adjusteData.length > 0) {
            adjusteData.push({
                deposito_descripcion: 'Total',
                cantidad: totalFormatted,
                isTotalRow: true
            });
        } else {
            adjusteData.push({
                deposito_descripcion: '-',
                cantidad: 'No Hay Stock'
            });
        }
    
        // Asignar los datos al dataSource de MatTableDataSource
        this.dataSource = new MatTableDataSource(adjusteData);
    }    

    groupAndAdjustData(data) {
        const groupedData = data.reduce((acc, articulo) => {
            if (articulo.stock !== '0') {
                const existing = acc.find(item => item.deposito_descripcion === articulo.deposito_descripcion);
                if (existing) {
                    existing.cantidad += ` + ${articulo.stock} ${articulo.um || ''}`.trim();
                } else {
                    acc.push({
                        deposito_descripcion: articulo.deposito_descripcion,
                        cantidad: `${articulo.stock} ${articulo.um || ''}`.trim()
                    });
                }
            }
            return acc;
        }, []);
    
        // Ajuste para separar los valores concatenados con espacio
        groupedData.forEach(item => {
            item.cantidad = item.cantidad.replace(/\s*\+\s*/g, ' + ');
        });
    
        return groupedData;
    }
    

    onCloseMenu() {
        this.dialogRef.close(false);
    }
}
